* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

.timer {
  display: flex;
  justify-content: space-between;
  max-width: 400px;
  margin: 20px auto;
}

.field {
  text-align: center;
}

.value {
  display: block;
  font-size: 2rem;
  font-weight: bold;
}

.label {
  font-size: 1rem;
  color: #555;
}
input, button {
  display: block;
  margin: 10px auto;
  padding: 10px;
  font-size: 1rem;
}
